.container {
    width: 100%;
}

.startScreenBGImage {
    height: 100vh;
    min-width: 100%;
    background-image: url("../componentIMG/gameOverBG.gif");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
}


.titleScreenHeader {
    text-align: center;
    color: rgb(225, 159, 59);
}

.titleScreenHeaderOnChange {
    text-align: center;
    animation: startButtonTextInitial .5s infinite ;
    color: rgb(84, 188, 233);
}

.startButton {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: auto;
    height: 60px;
    line-height: 1.5;
    margin: auto;
    background-color: aquamarine;
    border-radius: 13px;
    box-shadow: 0 0 15px #4CAF50;
    transition: box-shadow 0.3s ease-in-out;
}
.startButton:hover {
    box-shadow: 0 0 25px #4CAF50;
}

@keyframes startButtonTextInitial {
    0% {
        opacity: 1;
      }
      50% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
}

.startButtonText {
    animation: startButtonTextInitial 1.5s infinite;
}

@keyframes startButtonBlink {
    0% {
        opacity: 1;
      }
      50% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
}

.startButtonActivate {
    animation: startButtonBlink 0.1s infinite;
}

.bounce-in-bottom {
	animation: bounce-in-bottom 1.1s both;
}

@keyframes bounce-in-bottom {
    0% {
      transform: translateY(500px);
      animation-timing-function: ease-in;
      opacity: 0;
    }
    38% {
      transform: translateY(0);
      animation-timing-function: ease-out;
      opacity: 1;
    }
    55% {
      transform: translateY(65px);
      animation-timing-function: ease-in;
    }
    72% {
      transform: translateY(0);
      animation-timing-function: ease-out;
    }
    81% {
      transform: translateY(28px);
      animation-timing-function: ease-in;
    }
    90% {
      transform: translateY(0);
      animation-timing-function: ease-out;
    }
    95% {
      transform: translateY(8px);
      animation-timing-function: ease-in;
    }
    100% {
      transform: translateY(0);
      animation-timing-function: ease-out;
    }
  }

  .text-shadow-pop-top {
	animation: text-shadow-pop-top 2s both ;
    animation: startButtonTextInitial 1.5s infinite;
}

@keyframes text-shadow-pop-top {
    0% {
      text-shadow: 0 0 #555555, 0 0 #555555, 0 0 #555555, 0 0 #555555, 0 0 #555555, 0 0 #555555, 0 0 #555555, 0 0 #555555;
      transform: translateY(0);
    }
    100% {
      text-shadow: 0 -1px #555555, 0 -2px #555555, 0 -3px #555555, 0 -4px #555555, 0 -5px #555555, 0 -6px #555555, 0 -7px #555555, 0 -8px #555555;
      transform: translateY(8px);
    }
  }